export default {
  data() {
    return {
      payType: 1,
      orderInfo: {}
    };
  },
  computed: {

  },
  onLoad(options) {

  },

  methods: {
    //选择支付方式
    changePayType(type) {
      this.payType = type;
    },
    //确认支付
    confirm: async function () {
      uni.redirectTo({
        url: '/pages/money/paySuccess'
      })
    },
  }
}
